/*************/
//*! $ Imports
    //* |-> Shell
    import shell from 'shelljs'
    //* |-> Inquirer
    import inq from 'inquirer'
/*************/
//? -_ Creamos las rutas
const new_file = (name_file, type_leng, type_folder) => {
    //* |-> Path en donde se guardara el archivo
    const pathFile = `${process.cwd()}/src/${type_folder}/${name_file}.${type_folder}.${type_leng}`
    //* |-> Creamos el archivo
    shell.touch(pathFile)
}
//? -_ Realizara preguntas
const questions = () => {
    const ques = [
        {
            name: 'name_file',
            type: 'input',
            message: '¿Cual es el nombre del archivo?',
        },
        {
            name: 'type_leng',
            type: 'list',
            message: 'Tipo de lenguaje que utiliza:',
            choices: [ 'js', 'ts' ]
        },
        {
            name: 'type_folder',
            type: 'list',
            message: '¿Que intenta crear?',
            choices: [
                'controller',
                'database',
                'helper',
                'middleware',
                'model',
                'environments',
                'routes',
                'service'
            ]
        },
    ]
    return inq.prompt(ques)
}
//? -_ Ejecutara el script
export const exec_attach = async() => {
    //* |-> Preguntar sobre el nombre del archivo para la ruta
    const resp = await questions()
    const { name_file, type_leng, type_folder } = resp
    //* |-> Creamos una nueva ruta
    const create_attach = new_file(name_file, type_leng, type_folder)
}
